.button {
    padding: 15px 0;
    font: var(--font-size-s);
    position: relative;
    &:hover {
        opacity: 0.7;
    }
}


.blueSolid {
    color: #fff;
    background: var(--blue-color);
    text-transform: uppercase;
    border-radius: 9px;
    font-weight: 700;
}

.blueBorder {
    text-transform: uppercase;
    border-radius: 9px;
    font-weight: 700;
    background: transparent;
    color:  var(--blue-color);
    border: 2px solid var(--blue-color);
}


.clear,.clearBlue {
    padding: 0;
    background: transparent;
    &:hover {
        text-decoration: underline;
        opacity: 1;
    }
}


.clearBlue {
    color: var(--blue-color);
    font-weight: 700;
}

.icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 35px;
    border-radius: 13px;
    background: #FFF;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.15);
    padding: 7px 9px;

    svg {
        stroke: var(--black-color);
        fill: var(--black-color)
    }

    &:hover {
        background: var(--blue-color);
        opacity: 1;
        svg {
            stroke: #fff;
            fill: #fff
        }
    }
}


.disabled {
    opacity: 0.5;
}


.tooltip {
    position: absolute;
    min-width: 150px;
    top: 100%;
    left: 0;
    transform: translate(9px, 25%);
    background: var(--gray-bg-color);
    padding: 7px 9px;
    border: 1px solid var(--blue-color);
    border-radius: 5px;
    &::after,
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 0; 
        height: 0; 
    }

    &::before {
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 15px solid var(--gray-bg-color); 
        transform: translate(50%, -90%);
        z-index: 10;
    }
    &::after {
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 15px solid var(--blue-color); 
        transform: translate(50%, -98%);
        z-index: 9;
    }
}