
.icon {
    fill: var(--black-color);
    stroke: var(--black-color);
    height: 100%;
    
}

.iconContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 15px;
    width: 20px;
    height: 20px;
}


.link {
    display: flex;
    align-items: center;
    padding: 13px 0 13px 10px;

    &.active,
    &:hover {
        color: #fff;
        background: var(--blue-color);
        font-weight: 700;
        
    }
}

.link.active > .iconContainer > .icon,
.link:hover > .iconContainer > .icon {
    fill: #fff;
    stroke: #fff;
}

.collapsed {

    .link {
        justify-content: center;
    }

    .text {
        display: block;
        width: 0;
        overflow: hidden;
        transition: width 0.35s ease;
    }
}
